import store from '@/store'
import util from '@/utils/util'
import * as UserApi from '@/api/user'

export default {
	data() {
		return {
			wy_role_name: '',
			stewardPhone: ''
		}
	},
	methods: {

		//
		getUserInfo() {
			return new Promise((resolve,reject) => {
				UserApi.info().then(res => {
					this.wy_role_name = res.data.userInfo.wy_role_name 
					this.stewardPhone = res.data.userInfo.stewardPhone
					resolve()
				})
			})
		},

		onCall() {
			let that = this;
			this.getUserInfo().then(() => {
				if(this.wy_role_name == '') {
					uni.showModal({
						title: '',
						content: '\n()',
						showCancel: false,
						confirmText: '',
						//cancelText: '',
						success: function (res) {
							if (res.confirm) {
								//that.$navTo('pages/user/addRoom/index')
								that.$navTo('pages/index/index')
							} else if (res.cancel){
								that.$navTo('pages/index/index')
							}
						}
					});
				}else {
					uni.showModal({
						title: '',
						content: '\n'+that.stewardPhone,
						success: function (res) {
							if (res.confirm) {
								uni.makePhoneCall({
									phoneNumber: that.stewardPhone,
									success: () => {
										console.log('');
									},
									fail: (err) => {
										console.log('');
									}
								})
							}
						}
					});
				}
			})
		},

		onLink(linkObj) {
			if (!linkObj) return false
			// 
			if (linkObj.type === 'PAGE') {
				// 
				/*if(this.wy_role_name === '' && linkObj.title === '') {
					uni.showModal({
						title: '',
						content: '!',
						showCancel: false,
						confirmText: ''
					});
					return false
				}*/
				this.$navTo(linkObj.param.path, linkObj.param.query)
			}
			return true
		}
	},

}
