/**
 * requesthttps://ext.dcloud.net.cn/plugin?id=822
 */
import store from '@/store'
import request from './request'
import Config from '@/core/config'

// api
const apiUrl = Config.get('apiUrl')

// newrequest
const $http = new request({
  // 
  baseUrl: apiUrl,
  // 
  fileUrl: apiUrl,
  // url
  defaultUploadUrl: 'upload/image',
  // content-type
  header: {
    'content-type': 'application/json;charset=utf-8'
  },
  // , ms15000
  timeout: 15000,
  // 
  config: {
    // 
    isPrompt: true,
    // 
    load: true,
    // 
    isFactory: true
  }
})

// 
let requestNum = 0
// 
$http.requestStart = options => {
  if (options.load) {
    if (requestNum <= 0) {
      // 
      uni.showLoading({
        title: '',
        mask: true
      })
    }
    requestNum += 1
  }
  // 
  if (options.method == "FILE" && options.maxSize) {
    // : options.maxSize 
    const maxSize = options.maxSize
    for (let item of options.files) {
      if (item.size > maxSize) {
        setTimeout(() => {
          uni.showToast({
            title: "",
            icon: "none"
          })
        }, 10)
        return false
      }
    }
  }
  // 
  options.header['platform'] = store.getters.platform
  // Token
  options.header['Access-Token'] = store.getters.token
  // return false 
  return options
}

// 
$http.requestEnd = options => {
  // 
  if (options.load) {
    requestNum = requestNum - 1
    if (requestNum <= 0) {
      uni.hideLoading()
    }
  }
}

// modal
let loginModal = false

// 
// 
$http.dataFactory = async res => {
  // console.log("", {
  //   url: res.url,
  //   resolve: res.response,
  //   header: res.header,
  //   data: res.data,
  //   method: res.method,
  // })

  if (!res.response.statusCode || res.response.statusCode != 200) {
    // (catch)
    return Promise.reject({
      statusCode: res.response.statusCode,
      errMsg: 'http'
    })
  }

  let httpData = res.response.data
  if (typeof httpData == "string") {
    try {
      httpData = JSON.parse(httpData)
    } catch {
      httpData = false
    }
  }
  if (httpData === false || typeof httpData !== 'object') {
    // (catch)
    return Promise.reject({
      statusCode: res.response.statusCode,
      errMsg: "api"
    })
  }

  /*********()*********/

  // 
  // result.status [ 200 500 401 403 ]
  if (httpData.status == 200) {
    // (then)
    return Promise.resolve(httpData)
  }

  // 
  if (httpData.status == 401) {
    // 401, 
    store.dispatch('Logout')

    // 
    if (!loginModal) {
      loginModal = true
      // 
      uni.showModal({
        title: '',
        content: '1=1~',
        // showCancel: false,
        confirmText: "",
        cancelText: "",
        success: res => {
          if (res.confirm) {
            uni.navigateTo({
              url: "/pages/login/index"
            })
          }
          if (res.cancel && getCurrentPages().length > 1) {
            uni.navigateBack()
          }
          loginModal = false
        }
      })
    }
    // (catch)
    return Promise.reject({
      statusCode: 0,
      errMsg: httpData.message,
      result: httpData
    })
  }

  // 
  if (httpData.status == 500) {
    if (res.isPrompt) {
      setTimeout(() => {
        uni.showToast({
          title: httpData.message,
          icon: "none",
          duration: 2500
        }, 10)
      })
    }
    // (catch)
    return Promise.reject({
      statusCode: 0,
      errMsg: httpData.message,
      result: httpData
    })
  }

  /*********()*********/
}

// 
$http.requestError = e => {
  if (e.statusCode === 0) {
    throw e
  } else {
    setTimeout(() => showRequestError(e), 10)
  }
}

// 
const showRequestError = (e) => {
  let errMsg = `${e.errMsg}`
  console.log(e)

  if (e.errMsg === 'request:fail url not in domain list') {
    errMsg = 'API ' + e.errMsg
  }

  if (e.errMsg === 'request:fail') {
    errMsg = 'api'
  }
  uni.showToast({
    title: errMsg,
    icon: "none",
    duration: 3500
  })
}

export default $http
