import request from '@/utils/request'

// api
const api = {
  list: 'article/list',
  detail: 'article/detail'
}

// 
export function list(param, option) {
  return request.get(api.list, param, option)
}

// 
export function detail(articleId) {
  return request.get(api.detail, { articleId })
}
