import Config from '@/core/config'
import storage from '@/utils/storage'
import * as SettingApi from '@/api/setting'

const CACHE_KEY = 'Setting'
const OTHER = '_other'

// , 10
const setStorage = (data) => {
  const expireTime = 10 * 60
  storage.set(CACHE_KEY, data, expireTime)
}

// 
const getStorage = () => {
  return storage.get(CACHE_KEY)
}

//  ()
const getApiData = () => {
  return new Promise((resolve, reject) => {
    SettingApi.data()
      .then(result => {
        resolve(result.data.setting)
      })
  })
}

/**
 * 
 * , api
 * @param {bool} isCache  [api ]
 */
const data = isCache => {
  if (isCache == undefined) {
    isCache = Config.get('enabledSettingCache')
  }
  return new Promise((resolve, reject) => {
    const cacheData = getStorage()
    if (isCache && cacheData) {
      resolve(cacheData)
    } else {
      getApiData()
        .then(data => {
          setStorage(data)
          resolve(data)
        })
    }
  })
}

// ()
const item = (key, isCache) => {
  return new Promise((resolve, reject) => {
    data(isCache)
      .then(setting => {
        resolve(setting[key])
      })
  })
}

// H5
const h5Url = (isCache = false) => {
  return new Promise((resolve, reject) => {
    data(isCache)
      .then(setting => {
        const h5Url = setting[OTHER]['h5Url']
        resolve(h5Url)
      })
  })
}

export default {
  data,
  item,
  h5Url
}
